.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.topDisplay{
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  color:aliceblue;
  align-items: center;
  background-color: black;
  width: 100%;
  text-align: center;
  margin-top: -7%;
  margin-bottom: -7%;

}

.nav {
  display: flex;
}

.navButton {
  width: 40%;
  height: 100%;
  background-color: darkslateblue;
}

.navIcon {
  height: 50px;
  width: 50px;
}
